<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

  <h4>안녕 {{ $store.state.name }}</h4>
  <button @click="$store.commit('이름변경')">버튼</button>

  <Container @write="작성한글 = $event" :이미지="이미지" :게시물 ="게시물" :step="step" />
  
  <button @click="more">더보기</button>

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>




</template>

<script>
import Container from './components/Container'
import postdata from './assets/postdata.js'
import axios from 'axios'

export default { //서버에서 더보기 게시물을 가져옴
  name: "App",   //그 데이터를 HTML로 보여줄 것
  data(){        //더보기 버튼은 ajax를 써야함. 비동기 방식.
    return {
      step : 0,
      게시물 : postdata,
      이미지 : '',
      작성한글 : '',
      선택한필터 : '',
    }
  },

  mounted() {
    this.emitter.on('박스클릭함', (a)=> {
      this.선택한필터 = a
    })    
  },

  components:{
    Container, 
  },
  methods : {
    publish(){
      let 내게시물 = {
      name: "전호원",
      userImage: "https://placeimg.com/100/100/arch",
      postImage: this.이미지,
      likes: 36,
      date: "May 15",
      liked: false,
      content: this.작성한글,
      filter: this.선택한필터
    };
      this.게시물.unshift(내게시물);
      this.step = 0;
    },
    more(){
      //axios.post('URL', {name : 'kim'}).then().catch((err)=>{ err }) 
      //then은 성공시 catch는 실패시

      axios.get(`https://codingapple1.github.io/vue/more0.json`)
      .then( 결과 =>{ //(function(결과){
        console.log(결과.data);
        this.게시물.push(결과.data);
      })
    },
    upload(e){
      let file = e.target.files;
      console.log(file[0]);
      let url = URL.createObjectURL(file[0]);
      console.log(url);
      this.이미지 = url;
      this.step++;
      
    }
  },
};
</script>

